import React from "react";
import PropTypes from "prop-types";
import Tachyons from 'tachyons'

class EditFishForm extends React.Component {
  static propTypes = {
    fish: PropTypes.shape({
      image: PropTypes.string,
      name: PropTypes.string,
      desc: PropTypes.string,
      status: PropTypes.string,
      price: PropTypes.number
    }),
    index: PropTypes.string,
    updateFish: PropTypes.func
  };
  handleChange = event => {
    console.log(event.currentTarget.value);
    // update that fish
    // 1. Take a copy of the curernt fish
    const updatedFish = {
      ...this.props.fish,
      [event.currentTarget.name]: event.currentTarget.value
    };
    this.props.updateFish(this.props.index, updatedFish);
  };
  render() {
    return (
      <div className="fish-edit">
        <input
          type="text"
          name="name"
          onChange={this.handleChange}
          value={this.props.fish.name}
        />
        <input
          type="text"
          name="price"
          onChange={this.handleChange}
          value={this.props.fish.price}
        />
        <select
          type="text"
          name="status"
          onChange={this.handleChange}
          value={this.props.fish.status}
        >
          <option value="available">Available</option>
          <option value="unavailable">Leased</option>
        </select>
        <textarea
          name="desc"
          onChange={this.handleChange}
          value={this.props.fish.desc}
        />
        <input
          type="text"
          name="address"
          onChange={this.handleChange}
          value={this.props.fish.address}
        />
       <input
          type="text"
          name="suburb"
          onChange={this.handleChange}
          value={this.props.fish.suburb}
        /> 
       <input
          type="text"
          name="postcode"
          onChange={this.handleChange}
          value={this.props.fish.postcode}
        /> 
        {/* <span> */}
        <input className='tc w-40'
          type="text"
          name="contactName"
          onChange={this.handleChange}
          value={this.props.fish.contactName}
        />        
        <input className='tc w-30'
          type="text"
          name="phone"
          onChange={this.handleChange}
          value={this.props.fish.phone}
        />
        <input className='tc w-30'
          type="text"
          name="email"
          onChange={this.handleChange}
          value={this.props.fish.email}
        />
        {/* </span> */}
        {/* <input
          type="text"
          name="image"
          onChange={this.handleChange}
          value={this.props.fish.image}
        /> */}
        <button onClick={() => this.props.deleteFish(this.props.index)}>
          Remove Flat
        </button>
      </div>
    );
  }
}

export default EditFishForm;



